<template>
    <div class="v-home" v-if="route.name == 'home'">
        <img class="logo" :src="Logo" alt="深莓智能" />
        <span class="title">深莓智能运营管理平台</span>
        <a :href="path" class="button">点击进入</a>
    </div>
    <div class="m-container" v-else>
        <aside class="m-aside">
            <Navigation />
            <AsideSetting />
        </aside>
        <main class="m-main">
            <!-- 标题 -->
            <h2 class="m-title" v-if="label == 'details'">
                <span class="u-title-txt">
                    <el-page-header title="返回" @back="goBack" v-if="!hasArr" /> {{ detailName }}详情</span
                >
            </h2>
            <h2 class="m-title" v-else>
                <span class="u-title-txt">
                    <component class="u-title-icon" :is="state.view.icon" v-if="state.view.title" />{{
                        state.view.title
                    }}
                </span>
            </h2>
            <router-view />
        </main>
        <!-- 公共底部 -->
        <CommonFooter />
    </div>
</template>

<script setup>
import Logo from "@deepberry/common/img/logo.svg";
import CommonFooter from "@/components/common/footer.vue";
import Navigation from "@/components/common/commonNavigation.vue";
import AsideSetting from "@/components/asideSetting.vue";
import project from "../project.json";
import { storeToRefs } from "pinia";
import { reactive, toRaw, watch, computed } from "vue";
import { useRoute, useRouter } from "vue-router";
import { deepBerryStore } from "@/store/index";

// 跳转链接数据
const APPID = "ww5429d07e97752284";
const REDIRECT_URI = encodeURIComponent("https://admin.deepberry.cn/omp/#/role/details/0");
const path = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${APPID}&redirect_uri=${REDIRECT_URI}&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect`;

// 公共store
const store = deepBerryStore();
const route = useRoute();
const router = reactive(useRouter());
let { label, deepBerry } = storeToRefs(store);
// 存储store
label = toRaw(route).name;
deepBerry = project.pages;
store.label = label;
store.deepBerry = deepBerry;
// 自定数据
const state = reactive({
    view: {
        icon: "",
        title: "",
    },
});
// 标题
watch(
    label,
    (val) => {
        state.view.icon = deepBerry[val]?.icon || "Notebook";
        state.view.title = deepBerry[val]?.title || "成员列表";
    },
    {
        immediate: true,
    }
);
// 返回箭头
const hasArr = computed(() => route.params.type == "role" && route.params.id == 0);
// 标题
const detailName = computed(() => deepBerry[route.params.type].key_name);
// 返回列表
function goBack() {
    router.go(-1);
}

watch(route, (route) => {
    console.log("当前页面：", route.name);
});
</script>

<style lang="less">
@import "@/assets/css/app.less";
</style>
<style lang="less" scoped>
@import "@/assets/css/views/home.less";
</style>
